.modal-card {
  width: 30rem;
  margin: 50px auto;
  height: auto;
  z-index: 2;
  padding: 30px;
  position: relative;
}
.drag-container {
  width: 100%;
  border: 1px dotted black;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.drag-container-active {
  border: 2px dotted rgb(88, 206, 88);
  height: 200px;
}

.selected-container {
  height: 7rem;
  width: 100%;
}
.card-checkbox {
  display: inline-block;
  position: absolute;
  top: 2px;
  left: 2px;
  opacity: 0.6;
}
.my-card {
  width: 100%;
  position: relative;
}

.my-card img {
  object-fit: cover;
}

@media only screen and (max-width: 768px) {
  .modal-card {
    width: 20rem;
  }
}
